import React from "react";
import { graphql } from "gatsby";
import { RichText } from "prismic-reactjs";

import { CookieConsentFragment } from "@landingpage/core/src/components/CookieConsent/fragment";
import { TopbarFragment } from "@landingpage/core/src/components/Topbar/fragment";
import { FooterFragment } from "@landingpage/core/src/components/Footer/fragment";
import { MoviesFragment } from "@landingpage/core/src/components/Movies/fragment";

import { HeaderFragment } from "../components/Header/fragment";
import { ContentBlockHomePage } from "../components/ContentBlock/fragment";
import { ContentWithQuoteBoxFragment } from "../components/ContentWithQuoteBox/fragment";

import Layout from "@landingpage/core/src/components/Layout";
import SEO from "@landingpage/core/src/components/SEO";
import CookieConsent from "@landingpage/core/src/components/CookieConsent";
import Footer from "@landingpage/core/src/components/Footer";
import Topbar from "@landingpage/core/src/components/Topbar";

import Header from "../components/Header";
import Slices from "../components/Slices";

const Homepage = ({ data }) => {
  const page = data.prismicHomepage;
  if (!page) return null;

  return (
    <Layout>
      <SEO lang={page.lang} title={RichText.asText(page.data?.title?.raw)} />
      <CookieConsent
        cookieConsent={page.data?.cookie_consent?.document?.data}
      />
      <Topbar
        topbar={page.data?.topbar?.document?.data}
        logo={{ width: 120, height: 60 }}
      />
      <Header header={page.data?.header?.document?.data} />
      <Slices slices={page.data?.body} />
      <Footer
        year={page.last_publication_date}
        footer={page.data?.footer?.document?.data}
        borderTop
        logo={{ width: 140, heigth: 120 }}
      />
    </Layout>
  );
};

export const query = graphql`
  query HomepageById($id: String!) {
    prismicHomepage(id: { eq: $id }) {
      id
      lang
      last_publication_date(formatString: "Y")

      data {
        title {
          raw
        }
        cookie_consent {
          document {
            ...CookieConsentFragment
          }
        }
        topbar {
          document {
            ...TopbarFragment
          }
        }
        header {
          document {
            ...HeaderFragment
          }
        }
        footer {
          document {
            ...FooterFragment
          }
        }
        body {
          ...MoviesFragment
          ...ContentWithQuoteBoxFragment
          ...PricingFragment
          ...FaqFragment
          ...ContentBlockHomePage
        }
      }
    }
  }
`;

export default Homepage;
